import React from "react";
import PersistentDrawerLeft from "../src/components/toolbar";
import RoutingUtility from "../src/util/RoutingUtility";

export default function Root(props) {

  const navigationBar = {};

  return (
    <section style={navigationBar}>
      {PersistentDrawerLeft({
        browserRoutes : [{
          text: "Worklist",
          link: RoutingUtility.getRoutingLink("worklist")
        },{
          text: "Search Customer",
          link: RoutingUtility.getRoutingLink("searchCustomer")
        }]
      })}
    </section>
  );
}
